import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';
import Cross from '@avtopro/icons/dist/jsx/CrossIcon';

export default function Banner({ mode, className, header, children, onClose }) {
    const resultClassName = classNames({
        'pro-banner': true,
        [`pro-banner--${mode}`]: Boolean(mode),
        [className]: Boolean(className)
    });

    return (
        <div className={resultClassName}>
            {onClose ? <BannerClose onClick={onClose} /> : null}
            {header ? <div className="pro-banner__header">{header}</div> : null}
            {children ? <div>{children}</div> : null}
        </div>
    );
}

Banner.propTypes = {
    /**
     *        
     */
    mode: PropTypes.oneOf([
        '',
        'info',
        'error',
        'success',
        'warning',
        'question',
        'attention',
        'santa',
        'lamp'
    ]),
    /**
     *  CSS-
     */
    className: PropTypes.string,
    /**
     *  
     */
    header: PropTypes.string,
    /**
     *  
     */
    children: PropTypes.any,
    /**
     *   
     *  onClose ,     -   .
     */
    onClose: PropTypes.func
};

Banner.defaultProps = {
    mode: 'success',
    className: '',
    header: '',
    onClose: null,
    children: null
};

/**
 * -   
 */
function BannerClose(props) {
    return (
        <a className="pro-banner__close" role="button" tabIndex="0" {...props}>
            <Cross />
        </a>
    );
}
